tion {\n  cursor: pointer;\n  position: absolute;\n  top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 100000; // highest level within panel\n  display: none;\n\n  .root:hover > &,\n  // for screenshot tests\n  &:global(.hoverForScreenshot) {\n    background-color: rgba(45, 45, 51, 1);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-wrap: wrap;\n  }\n\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: flex-end;\n  font-size: 14px;\n  padding-top: 24px;\n\n  div {\n    width: 100%;\n    padding: 6px 0;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    flex-wrap: wrap;\n  }\n\n  svg {\n    margin-right: 4px;\n    width: 24px;\n    height: 24px;\n    fill: white;\n  }\n\n  button {\n    width: 72px;\n    height: 72px;\n    margin: 4px;\n    flex: none;\n    font-size: 14px;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    background: rgba(110, 81, 238, 0.5);\n    svg {\n      margin: 0 0 6px;\n    }\n\n    &:not(:global(.disabled)):hover {\n      background: rgba(110, 81, 238, 0.8);\n    }\n  }\n\n  p {\n    font-size: 12px;\n    color: $text-muted;\n  }\n}\n\n.prohibitedSelection {\n  padding: 10px;\n\n  .root:hover &,\n  // for screenshot tests\n  &:global(.hoverForScreenshot) {\n    background-color: rgba(45, 45, 51, 1);\n  }\n}\n\n.tabActionsOverlay button {\n  width: 145px;\n  height: 40px;\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n}\n\n.exitFullScreen {\n  position: fixed;\n  top: 75px;\n  right: 8px;\n  z-index: 102;\n  opacity: 1;\n  background-color: $dark3;\n  display: none;\n  .root:hover & {\n    display: block;\n  }\n  &:global(.hoverForScreenshot) {\n    display: block;\n  }\n  svg {\n    width: 16px;\n    height: 16px;\n    fill: white;\n    float: left;\n  }\n  span {\n    float: right;\n    padding-left: 3px;\n  }\n}\n","$topBarHeight: 40px;\n$playbackContr